import { _fetch, host  } from './index';

export const fetchSignParams = (url = window.location.href) => (
	_fetch(`${host}/codesquare/weixin/get-sign-package`, {
		body: {
			url
		}
	})
	// _fetch(`/wx/codesquare/weixin/get-sign-package`)
)

export const fetchUserInfo = () => (
	_fetch(`${host}/codesquare/site/get-user-info`)
);

export const fetchGetTopic = (params) => (
	_fetch(`${host}/codesquare/topic/index`, {
		body: params,
	})
);

export const fetchReviewTopic = (params) => (
	_fetch(`${host}/codesquare/topic/topic-detail`, {
		body: params,
	})
);
/**
 * flag === 1 取消点赞
 * flag !== 1 点赞
 * 
 */ 
export const fetchToggleLike = (flag = 1, topic_id, token) => {
	let target = flag === 1 ? 
		`${host}/codesquare/topic/cancel-like` : 
		`${host}/codesquare/topic/like`;

	return _fetch(target, {
		body: {
			topic_id,
			token,
		}
	});
}
/**
 * flag === 1 取消收藏
 * flag !== 1 收藏
 * 
 */ 
export const fetchToggleCollect = (flag = 1, topic_id, token) => {
	let target = flag === 1 ? 
		`${host}/codesquare/topic/cancel-collect` : 
		`${host}/codesquare/topic/collect`;

	return _fetch(target, {
		body: {
			topic_id,
			token,
		}
	});
}


